.overlay-panel {
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 0;
  text-align: center;
  top: 0;
  height: 100%;
  width: 40%;
  left: calc(100% - 40%);
  z-index: 5;
  background: linear-gradient(to right, #eb0784, #ef5230);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
}

